	module.exports = {"data":{"site":{"siteMetadata":{"title":"Matthew Brown's JS Blog","author":"Matthew Brown"}},"markdownRemark":{"id":"/home/travis/build/mbrown333/mbrown333.github.io/src/pages/20180608-a-new-beginning/index.md absPath of file >>> MarkdownRemark","html":"<h2>New Blog!</h2>\n<p>If you’ve checked out my blog before you’ll likely notice things look pretty different.  I’m currently in the middle of the process of re-platforming my blog away from Wordpress and now will be using Gatsby.  I’ve started the process of porting over my previous posts and will continue to do so until everything has been moved over.</p>\n<h2>Why Gatsby?</h2>\n<p>Good question.</p>\n<p>If you haven’t heard of <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> yet it’s a static site generator that uses React.js.  The main reason I switched to Gatsby is that it is blazing fast.  And it is very easy to use <a href=\"https://travis-ci.org/\">Travis CI</a> to deploy a Gatsby site to Github pages.  So another win is that hosting will be free.  Not that it was a lot before, but free beats cheap in my book.</p>\n<p>If you are interested in learning to do this yourself <a href=\"https://medium.freecodecamp.org/how-to-write-a-blog-using-gatsby-from-your-phone-e92a99851a04\">I recommend you check out this article for details on how</a>.</p>\n<h2>Stay Tuned</h2>\n<p>One of the reasons I’m excited about this move is it makes it much easier for me to deliver new content as I can focus less on administration and more on creating content.  So keep an eye out for more coming soon!</p>","frontmatter":{"title":"A New Beginning...","date":"June 08, 2018"}}},"pathContext":{"slug":"/20180608-a-new-beginning/","previous":{"fields":{"slug":"/20180409-functional-stateless-components-in-react/"},"frontmatter":{"title":"Functional Stateless Components in React"}},"next":{"fields":{"slug":"/20180608-immutability-in-javascript/"},"frontmatter":{"title":"Immutability in Javascript"}}}}

